'use client'

import { useState, useEffect } from 'react'
import { apiKeysApi, ApiError } from '@/lib/api-client'
import { Key, Loader2, AlertCircle, Plus } from 'lucide-react'

export default function KeysPage() {
  const [apiKeys, setApiKeys] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [showCreate, setShowCreate] = useState(false)

  useEffect(() => {
    loadData()
  }, [])

  const loadData = async () => {
    try {
      setLoading(true)
      setError(null)
      const response = await apiKeysApi.list()
      setApiKeys(response.apiKeys || [])
    } catch (err) {
      handleError(err)
    } finally {
      setLoading(false)
    }
  }

  const handleError = (err: any) => {
    if (err instanceof ApiError) {
      const errorMsg = err.status === 401 
        ? 'Invalid API key. Please check your API key and try again.'
        : `${err.message} (Status: ${err.status}, Code: ${err.code})`
      setError(errorMsg)
    } else if (err instanceof Error) {
      setError(`Failed to load data: ${err.message}`)
    } else {
      setError('Failed to load data: Unknown error')
    }
  }

  if (loading) {
    return (
      <div className="text-center py-12">
        <Loader2 className="w-8 h-8 animate-spin mx-auto text-lime-400" />
        <p className="mt-4 text-gray-400">Loading API keys...</p>
      </div>
    )
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-bold">API Keys</h1>
        <button
          onClick={() => setShowCreate(true)}
          className="flex items-center gap-2 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
        >
          <Plus className="w-4 h-4" />
          Create API Key
        </button>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {showCreate && (
        <CreateApiKeyModal
          onClose={() => {
            setShowCreate(false)
            loadData()
          }}
        />
      )}

      {apiKeys.length === 0 ? (
        <div className="text-center py-12 bg-gray-900 rounded-lg border border-gray-800">
          <Key className="w-12 h-12 text-gray-600 mx-auto mb-4" />
          <p className="text-gray-400 mb-4">No API keys yet</p>
          <button
            onClick={() => setShowCreate(true)}
            className="px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
          >
            Create Your First API Key
          </button>
        </div>
      ) : (
        <div className="grid gap-4">
          {apiKeys.map((key) => (
            <ApiKeyCard key={key.id} apiKey={key} onRefresh={loadData} />
          ))}
        </div>
      )}
    </div>
  )
}

function ApiKeyCard({ apiKey, onRefresh }: { apiKey: any; onRefresh: () => void }) {
  const handleRevoke = async () => {
    if (!confirm('Are you sure you want to revoke this API key?')) return
    
    try {
      await apiKeysApi.revoke(apiKey.id)
      onRefresh()
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to revoke API key')
    }
  }

  return (
    <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
      <div className="flex items-start justify-between">
        <div className="flex-1">
          <div className="flex items-center gap-3 mb-2">
            <h3 className="text-xl font-semibold">{apiKey.name}</h3>
            <span className={`px-2 py-1 text-xs rounded ${
              apiKey.role === 'admin'
                ? 'bg-purple-900/30 text-purple-400'
                : apiKey.role === 'user'
                ? 'bg-blue-900/30 text-blue-400'
                : 'bg-gray-800 text-gray-400'
            }`}>
              {apiKey.role}
            </span>
            {apiKey.revokedAt && (
              <span className="px-2 py-1 text-xs rounded bg-red-900/30 text-red-400">
                Revoked
              </span>
            )}
          </div>
          <div className="grid grid-cols-2 gap-4 text-sm mt-4">
            <div>
              <span className="text-gray-500">Rate Limit:</span>
              <p className="text-gray-300 mt-1">{apiKey.rateLimit} requests/hour</p>
            </div>
            <div>
              <span className="text-gray-500">Last Used:</span>
              <p className="text-gray-300 mt-1">
                {apiKey.lastUsedAt 
                  ? new Date(apiKey.lastUsedAt).toLocaleDateString()
                  : 'Never'}
              </p>
            </div>
            <div>
              <span className="text-gray-500">Created:</span>
              <p className="text-gray-300 mt-1">
                {new Date(apiKey.createdAt).toLocaleDateString()}
              </p>
            </div>
            {apiKey.expiresAt && (
              <div>
                <span className="text-gray-500">Expires:</span>
                <p className="text-gray-300 mt-1">
                  {new Date(apiKey.expiresAt).toLocaleDateString()}
                </p>
              </div>
            )}
          </div>
        </div>
        {!apiKey.revokedAt && (
          <button
            onClick={handleRevoke}
            className="px-3 py-1.5 text-sm bg-red-900/20 text-red-400 rounded hover:bg-red-900/30 transition-colors"
          >
            Revoke
          </button>
        )}
      </div>
    </div>
  )
}

function CreateApiKeyModal({ onClose }: { onClose: () => void }) {
  const [loading, setLoading] = useState(false)
  const [createdKey, setCreatedKey] = useState<string | null>(null)
  const [formData, setFormData] = useState({
    name: '',
    role: 'user' as 'admin' | 'user' | 'read-only',
    rateLimit: 1000,
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    
    try {
      const response = await apiKeysApi.create(formData)
      setCreatedKey(response.token)
    } catch (err) {
      alert(err instanceof ApiError ? err.message : 'Failed to create API key')
    } finally {
      setLoading(false)
    }
  }

  if (createdKey) {
    return (
      <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
        <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-md w-full mx-4">
          <h3 className="text-xl font-bold mb-4">API Key Created!</h3>
          <p className="text-yellow-400 mb-4 text-sm">
            ⚠️ Save this key securely. It will not be shown again.
          </p>
          <div className="bg-gray-800 rounded-lg p-4 mb-4">
            <code className="text-lime-400 break-all">{createdKey}</code>
          </div>
          <button
            onClick={() => {
              navigator.clipboard.writeText(createdKey)
              alert('Copied to clipboard!')
            }}
            className="w-full px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors mb-2"
          >
            Copy to Clipboard
          </button>
          <button
            onClick={onClose}
            className="w-full px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors"
          >
            Done
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-gray-900 rounded-lg border border-gray-800 p-6 max-w-md w-full mx-4">
        <h3 className="text-2xl font-bold mb-6">Create API Key</h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Name *
            </label>
            <input
              type="text"
              required
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Role
            </label>
            <select
              value={formData.role}
              onChange={(e) => setFormData({ ...formData, role: e.target.value as any })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            >
              <option value="read-only">Read Only</option>
              <option value="user">User</option>
              <option value="admin">Admin</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              Rate Limit (requests/hour)
            </label>
            <input
              type="number"
              value={formData.rateLimit}
              onChange={(e) => setFormData({ ...formData, rateLimit: parseInt(e.target.value) })}
              className="w-full px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white focus:outline-none focus:border-lime-400"
            />
          </div>
          <div className="flex gap-3 pt-4">
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-4 py-2 bg-lime-400 text-gray-900 rounded-lg font-medium hover:bg-lime-300 transition-colors disabled:opacity-50"
            >
              {loading ? 'Creating...' : 'Create Key'}
            </button>
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-gray-800 text-gray-300 rounded-lg hover:bg-gray-700 transition-colors"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}






